import React from 'react';
import './Service.css';
import HeartEmoji from '../../assets1/heartemoji.png';
import Glasses from '../../assets1/glasses.png';
import Humble from '../../assets1/humble.png';
import Resume from '../../assets1/bittu resume.pdf';
import Card from '../Card/Card';
import { themeContext } from '../../Context';
import { useContext } from 'react';
import {motion} from 'framer-motion';
function Service() {
  const transition ={duration : 1, type: 'spring'}
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;
  return (
    <div className='Service' id='services'>
      {/* Left side */}
      <div className='awesome' >
      <span style={{color: darkMode? 'white': ''}}>My Awesome</span>
      <span>Service</span>
      <span  style={{color: darkMode? 'white': ''}}>As a MERN stack developer, the services you can provide 
        < br/>
        typically revolve around building web applications
        <br /> using the MongoDB, Express.js, React.js, and Node.js stack. 
      </span>
      <a href={Resume} download>
      <button className='button s-button'> Download CV</button>
      </a>
      <div className='blur s-blur1' style={{background: "#ABF1FF94"}}></div>
      </div>
      {/* right side */}
      <div className='cards'>

      <motion.div 
  initial={{ left: "25rem" }}
  whileInView={{ left: "14rem" }}
  transition={transition}
      style={{left: '14rem'}}>
        <Card
        emoji = {HeartEmoji}
        heading = {'Design'}
        detail = {'Figma, sketch, photoshop, adobe, Adobe xd'}
        />
      </motion.div>

      {/* Second card */}

      <motion.div 
      initial={{ left: "-11rem", top: "12rem" }}
      whileInView={{ left: "-4rem" }}
      transition={transition}
      style={{top:"12rem",left: '-4rem'}}>
        <Card
        emoji = {Glasses}
        heading = {'Developer'}
        detail = {'Html, Ccs, JavaScript, React, Node.js,Express.js,MongoDB'}
        />
      </motion.div>    

       {/* Third card */}

       <motion.div
        initial={{ top: "19rem", left: "25rem" }}
        whileInView={{ left: "12rem" }}
        transition={transition}
       style={{top:"19rem",left: '12rem'}}>
        <Card
        emoji = {Humble}
        heading = {'UI/UX'}
        detail = {'Html, Ccs, JavaScript, React, Node.js,Express.js,MongoDB'}
        />
      </motion.div>   
  <div className='blur s-blur2' style={{ background:'var(--purple)' }}></div>
</div>
    </div>
  )
}

export default Service
