import React, { useState } from 'react'
import './Contact.css';
import emailjs from '@emailjs/browser';
import { useRef } from 'react'
import { themeContext } from '../../Context';
import { useContext } from 'react';

function Contact() {
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;
  const form = useRef();
  const [ done, setDone ] = useState(false)

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm('service_ziicofi', 'template_d6lcq6o', form.current, {
        publicKey: 'wWFBA8bH8TlNO7gtA',
      })
      .then(
        () => {
          console.log('SUCCESS!');
          setDone(true);
         
        },
        (error) => {
          console.log('FAILED...', error.text);
        },
      );
  };
  return (
    <div className='contact-form' id='contact'>
      <div className="w-left">
        <div className="awesome">
          <span style={{color: darkMode? 'white': ''}}>Get in touch</span>
          <span>Contact me</span>
          <div>
          <div className="blur t-blur1" style={{background: "#ABF14FF94"}}></div>
     
          </div>
        </div>
      </div>
      <div className="c-right">
        <form ref={form} onSubmit={sendEmail} >
          <input type='text' name='user_name' className='user' placeholder='Name' /> 
          <input type='text' name='user_email' className='user' placeholder='Email' />
          <textarea name='message' className='user' placeholder='Message' />
          <input type="submit" value="Send" className='button' />
          <span>{done && "Thanks for contacting me!"}</span>
          <div className='blur c-blur1' style={{background: "var(--purple)"}}></div>
        </form>
      </div>
    </div>
  )
}

export default Contact
