import React from 'react'
import './portfoliyo.css';
import {Swiper, SwiperSlide}from 'swiper/react';
import Sidebar from '../../assets1/sidebar.png';
import Ecommerce from '../../assets1/ecommerce.png';
import HOC from '../../assets1/hoc.png';
import MusicApp from '../../assets1/musicapp.png';
import 'swiper/css'
import { themeContext } from '../../Context';
import { useContext } from 'react';

function Portfoliyo() {
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;
  return (
    <div className=' Portfoliyo' id='portfolio'>
     {/* heading */}
     
     <span style={{color: darkMode? 'white': ''}}>Recent Projects</span>
     <span>Portfolio</span>

{/* slider */}
  <swiper 
   spaceBetwwn={30}
   slidesPreView={2}
   grabCursor={true}
   className='Portfoliyo-slider'
  >
    <swiperslide>
        <img src={Sidebar} />
    </swiperslide>
    <swiperslide>
        <img src={Ecommerce} />
    </swiperslide>{" "}
    <swiperslide>
        <img src={HOC} />
    </swiperslide>
    <swiperslide>
        <img src={MusicApp} />
    </swiperslide>
   
  </swiper>
    </div>
  )
}

export default Portfoliyo
