import React from 'react';
import './intro.css';
import crown from '../../assets1/crown.png';
import thumbup from '../../assets1/thumbup.png';
import glassesimoji from '../../assets1/glassesimoji.png';
import FloatingDiv from '../FloatingDiv/Float';
import { themeContext } from '../../Context';
import { useContext } from 'react';
import {motion} from 'framer-motion';
import { Link } from 'react-scroll';

function Intro() {
 
  const transition = { duration: 2, type: "spring" };


  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;
  return (
<div className='intro' id='Navbar'>
<div className='i-left'>
<div className='i-name'>
    <span style={{color: darkMode? 'white':''}}> Hy! I Am</span>
    <span>Roshni Sonkar</span>
    <span style={{color: darkMode? 'white':''}}>I'm MERN Stack Developer which stands for MONGODB, EXPRESS.JS, REACT.JS, NODE.JS.</span>
</div>

<Link to="contact" smooth={true} spy={true}>
          <button className="button i-button">Hire me</button>
        </Link>
<div className='i-icons'>
    <a href='https://github.com/Roshnisonkar'>
    <img src='..\assets\img\github.png' />
    </a>

    <a href='https://www.linkedin.com/in/roshni-sonkar-2b6604273'>
    <img src='..\assets\img\linkedin.png' />
    </a>
    
    <a href='https://www.instagram.com/___bittu___13/'>
    <img src='..\assets\img\instagram.png' />
    </a>
</div>

</div>
<div className='i-right' >
<img src='..\assets\img\Vector1.png'  />
 <img src='..\assets\img\Vector2.png' />
 <img src='..\assets\img\boy.png ' className='girl' />
 <motion.img
   initial={{ left: "-36%" }}
   whileInView={{ left: "-24%" }}
   transition={transition}
 src={glassesimoji}/>


<motion.div
      initial={{ top: "-4%", left: "74%" }}
      whileInView={{ left: "68%" }}
      transition={transition}

style={{top : '-4%', left:'68%'}}
className='floating-div'
>
    <FloatingDiv  img = {crown} text1= 'Web' text2 = 'Developer'/>
</motion.div>

<motion.div
  initial={{ left: "9rem", top: "18rem" }}
  whileInView={{ left: "0rem" }}
  transition={transition}
style={{top: '18rem', left:'0rem'}}
floating-div>
  <FloatingDiv img = {thumbup}  text1='Best Design' text2='Award'/>
</motion.div>
</div>
{/* Blur div*/}
<div className='blur' style={{background:"rgb (238 210 255)" }}></div>
<div className='blur' 
style={{
  background: '#C1F5FF',
 top:'17rem',
 width:'21rem',
 height: '11rem',
 left: 'z8em'
}}></div>
</div>
  )
}

export default Intro
